import { IoCloseSharp } from 'react-icons/io5';
import styles from '../styles/panels.module.css';

function PanelHeader ({
  label = 'Panel',
  onClose = () => {},
}) {
  return (
    <div className={styles.panelHeader}>
      <span>
        <a onClick={onClose}><IoCloseSharp/></a>
      </span>
      {label}
    </div>
  )
}

function PanelContent ({ children }) {
  return (
    <div className={styles.panelContent}>
      {children}
    </div>
  )
}

export function Panel ({
  label = 'Panel',
  children = null,
  id = null,
  onClose = null,
}) {
  return (
    <div
      data-id={id}
      className={styles.panel}
    >
      <PanelHeader label={label} onClose={onClose} />
      <PanelContent children={children} />
    </div>
  )
}

export function Panels ({
  children = null,
  horizontal = false,
  vertical = false,
  id = null,
}) {
  return (
    <div
      data-id={id}
      className={[
        styles.panels,
        horizontal ? styles.panelsAreHorizontal : '',
        vertical ? styles.panelsAreVertical : '',
      ].join(' ')}
    >
      {children}
    </div>
  )
}